.publicar {
    width: clamp(30%, 1000px, 90%);
    min-height: 52rem;
    margin: 0 auto;
}

.publicar p {
    color: var(--gray);
    font-size: 1.5rem;
    font-family: var(--paragraphs);
}

.publicar h2 {
    text-align: left;
    padding-bottom: 2rem;
    border-bottom: 2px solid var(--b-gray);
    font-size: 3rem;
}

.publicar h3 {
    color: var(--black);
    font-size: 2rem;
    font-family: var(--titles);
}

.publicar h4 {
    color: var(--black);
    font-size: 1.7rem;
    font-family: var(--titles);
}

.publicar section {
    margin: 4rem 0 8rem;
}

.publicar form  {
    margin-bottom: 6rem;
}

.publicar .buttons {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.publicar .titulo__seccion {
    text-align: center;
    padding: 0;
    margin: 4rem 0;
    border: none;
    font-size: 4rem;
}